import gcoord from 'gcoord';
// 转换经纬度
export function getLonLat (marketList) {
  marketList.forEach(item => {
    let [Lon, Lat] = bdTfGc(item.Longitude, item.Latitude)
    item.Longitude = Lon
    item.Latitude = Lat
  });
  return marketList
}

export function getHetMapLonLat (marketList) {
  marketList.forEach(item => {
    let [Lon, Lat] = bdTfGc(item.lng, item.lat)
    item.lng = Lon
    item.Lat = Lat
  });
  return marketList
}

// 百度转火星
export function bdTfGc (c, d) {
  var result = gcoord.transform(
    [c, d],    // 经纬度坐标
    gcoord.BD09,
    gcoord.GCJ02
  );
  return result
}

// 地球转火星，ios签到专用
export function WGTfGc (c, d) {
  var result = gcoord.transform(
    [c, d],    // 经纬度坐标
    gcoord.WGS84,
    gcoord.GCJ02
  );
  return result
}